@import url(https://fonts.googleapis.com/css?family=Montserrat:100,300,400,400i,700,900);@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.5.0/css/font-awesome.min.css);@import url(https://cdn.jsdelivr.net/themify-icons/0.1.2/css/themify-icons.css);
html,
body {
  height: 100%;
}
.main-header {
  position: fixed;
  width: 100%;
  -webkit-box-shadow: 2px 4px 20px -4px rgba(0, 0, 0, 0.1);
  box-shadow: 2px 4px 20px -4px rgba(0, 0, 0, 0.1);
}
.main-header .navbar {
    min-height: 65px;
    height: 65px;
}
.main-header .navbar .navbar-custom-menu .navbar-nav > li > a,
    .main-header .navbar .navbar-right > li > a {
      border-left: 0 !important;
      border-right: 0 !important;
      padding: 22.5px 15px;
}
.main-header .sidebar-toggle {
    padding: 19px 22.5px;
    font-size: 18px;
    border-left: 0;
    border-right: 0 !important;
}
.main-header .sidebar-toggle:focus, .main-header .sidebar-toggle:active {
      background: transparent;
}
.main-header .sidebar-toggle .icon-bar {
      display: none;
}
.main-header .logo {
    height: 65px;
    line-height: 65px;
}
.content-header {
  position: relative;
  padding: 77px 15px 12px 15px;
  -webkit-box-shadow: 0 4px 20px 0 rgba(0, 0, 0, 0.2);
          box-shadow: 0 4px 20px 0 rgba(0, 0, 0, 0.2);
}
@media (max-width: 767px) {
.content-header {
    padding: 142px 15px 12px 15px;
}
}
.main-sidebar {
  position: fixed;
  padding-top: 65px;
  width: 230px;
}
.main-sidebar .sidebar-menu li > a {
    padding: 10px 5px 10px 15px;
    display: block;
}
.main-sidebar .sidebar-menu li > a i {
      margin-right: 5px;
}
.main-sidebar .sidebar-menu .treeview-menu li > a {
    padding: 10px 5px 10px 30px;
}
.main-sidebar .sidebar-menu .treeview-menu li > a i {
      margin-right: 5px;
}
.main-sidebar .user-panel {
    padding: 10px 10px 60px;
    text-align: center;
    -webkit-transition: -webkit-transform 0.3s ease-in-out, width 0.3s ease-in-out;
    -webkit-transition: width 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
    transition: width 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out, width 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out, width 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}
.main-sidebar .user-panel .bg-user {
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
      width: 100%;
      height: 100%;
      opacity: 0.3;
      padding: 0;
      margin: 0;
      background-size: cover;
}
.main-sidebar .user-panel:before {
      content: " ";
      display: table;
}
.main-sidebar .user-panel:after {
      content: " ";
      display: table;
      clear: both;
}
.main-sidebar .user-panel > .image > img {
      width: 100%;
      max-width: 100px;
      height: auto;
}
.main-sidebar .user-panel > .info {
      padding: 15px 5px 15px 15px;
      line-height: 1;
      position: static;
}
.main-sidebar .user-panel > .info > p {
        font-weight: 300;
        margin-bottom: 9px;
        font-size: 14px;
        letter-spacing: 1px;
}
.main-sidebar .user-panel > .info > p small {
          letter-spacing: 0;
          font-size: 10px;
}
.main-sidebar .user-panel > .info > div {
        background: rgba(31, 45, 61, 0.5);
        position: absolute;
        width: 100%;
        bottom: 0;
        left: 0;
        padding: 15px 0;
}
.main-sidebar .user-panel > .info > div a {
          margin: 0 10px;
          color: rgba(255, 255, 255, 0.9);
          display: inline-block;
          text-align: center;
}
.main-sidebar .user-panel > .info > div a small {
            padding-top: 10px;
            display: inline-block;
            position: relative;
            top: 0;
}
.main-sidebar .user-panel > .info > div a i {
            font-size: 20px;
            display: block;
}
.main-sidebar .user-panel > .info > a {
        text-decoration: none;
        padding-right: 5px;
        margin-top: 3px;
        font-size: 11px;
}
.main-sidebar .user-panel > .info > a > .fa,
        .main-sidebar .user-panel > .info > a > .ion,
        .main-sidebar .user-panel > .info > a > .glyphicon {
          margin-right: 3px;
}
.sidebar-collapse .treeview.active,
.sidebar-collapse .user-panel {
  width: 50px !important;
}
.sidebar-collapse .user-panel {
  padding: 10px 10px 10px;
}
.sidebar-collapse .treeview {
  width: 50px !important;
  padding-left: 0;
}
.sidebar-collapse .treeview-menu {
  padding-left: 0;
}
.sidebar-mini:not(.sidebar-mini-expand-feature).sidebar-collapse .sidebar-menu > li:hover > a > span {
  padding: 10px 5px 10px 20px;
}
.sidebar-mini:not(.sidebar-mini-expand-feature).sidebar-collapse .sidebar-menu > li:hover > .treeview-menu {
  top: 38px;
}
@media (max-width: 767px) {
.sidebar-open .main-sidebar {
    padding-top: 130px;
}
}
.main-footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
}
